</template>
<script>
	import ProductItem from '@/components/product-item/ProductItem.vue'
	const app = getApp()
	export default {
		data() {
			return {
				baseUrl: this.$baseURL,
				carouselImages: [],
				cateList: [], // 所有类目
				newPersonList: [],
				isShow:0,
				joinCart:false,
				productList: [],
				noMoreData: false,
				page: 1,
				pageSize: 5,
				records: 0,
				quantity:1,
				reportFile:[],
				reportFileShow:false,
			}
		},
		components: {
			ProductItem,
		},
		mounted(option) {
			this.getCarouselImages();
			this.getCates();
			this.getNewPerson()
			this.getProductList()
		},
		methods: {
			loadMoreData() {
				if (this.noMoreData) return
				this.page = this.page + 1
				this.getProductList()
			},
			async getProductList() {
				try {
					const { rows } = await this.$http.post('/items/recommend', {
						page: this.page,
						pageSize: this.pageSize,
					})
					// 加载完了
					if (!rows.length) {
						this.noMoreData = true
						return
					} 
					this.productList = rows
				} catch (error) {
					this.productList = []
				}
			},
			async getCarouselImages() {
				try {
				console.log(11111)
					this.carouselImages = await this.$http.post('/index/carousel', {})
				} catch (error) {
					this.carouselImages = []
				}
			},
			async getCates() {
				try {
					this.cateList = await this.$http.post('/index/cats', {})
				} catch (error) {
					this.cateList = []
				}
			},
			async getNewPerson() {
				try {
					const { rows } = await this.$http.post('/items/newperson', {
						page: 1,
						pageSize: 10
					})
					this.newPersonList = rows.slice(0, 3)
				} catch (error) {
					this.newPersonList = []
				}
			},
			decrease() {
				 if (this.quantity > 1) {
				            this.quantity--;
				}
			}
		},

	}
</script>

<style>
	@import url(/pages/index/index.css);
